import React, {useState} from 'react';
import {v1} from 'uuid';
import {StoreType} from '../../../Store/Store';
import {Button, ButtonGroup, Grid, Paper} from '@material-ui/core';

export type CategoryListPropsType = {
    store: StoreType
    category: string
}
export type CollapsedType = {
    [key: string]: ValueType[]
}
export type ValueType = {
    id: number
    valueAction: boolean
    valueIngred: boolean
    valueApply: boolean
}

export const CategoryList = (props: CategoryListPropsType) => {
    const specialProductsItems = props.store.items.filter(f => f.category === props.category)
    const [bottonsValue, setBottonsValue] = useState<CollapsedType>({
            ['372']: [{id: 372, valueAction: true, valueIngred: false, valueApply: false}],
            ['306']: [{id: 306, valueAction: true, valueIngred: false, valueApply: false}],
            ['832']: [{id: 832, valueAction: true, valueIngred: false, valueApply: false}],
            ['1111']: [{id: 1111, valueAction: true, valueIngred: false, valueApply: false}],
            ['991312']: [{id: 991312, valueAction: true, valueIngred: false, valueApply: false}],
            ['889']: [{id: 889, valueAction: true, valueIngred: false, valueApply: false}],
            ['367']: [{id: 367, valueAction: true, valueIngred: false, valueApply: false}],
            ['888']: [{id: 888, valueAction: true, valueIngred: false, valueApply: false}],
            ['369']: [{id: 369, valueAction: true, valueIngred: false, valueApply: false}],
            ['991039']: [{id: 991039, valueAction: true, valueIngred: false, valueApply: false}],
            ['111']: [{id: 111, valueAction: true, valueIngred: false, valueApply: false}],
            ['222']: [{id: 222, valueAction: true, valueIngred: false, valueApply: false}],
            ['223']: [{id: 223, valueAction: true, valueIngred: false, valueApply: false}],
        }
    )

    const onClickChanger = (id: number, valueOfButton: "valueAction" | 'valueIngred' | 'valueApply') => {
        switch (valueOfButton) {
            case 'valueAction':
                setBottonsValue({
                    ...bottonsValue,
                    [id.toString()]: bottonsValue[id.toString()].map(m => ({...m, valueAction: !m.valueAction, valueIngred: false, valueApply: false}))
                })
                break
            case 'valueIngred':
                setBottonsValue({
                    ...bottonsValue,
                    [id.toString()]: bottonsValue[id.toString()].map(m => ({...m, valueAction: false, valueIngred: !m.valueIngred, valueApply: false}))
                })
                break
            case 'valueApply':
                setBottonsValue({
                    ...bottonsValue,
                    [id.toString()]: bottonsValue[id.toString()].map(m => ({...m, valueAction: false, valueIngred: false, valueApply: !m.valueApply}))
                })
                break
            default:
                console.log("Ошибка")
                break
        }

    }

    return (
        <Grid container justifyContent={'center'} spacing={1}>
            {specialProductsItems.map(m => {
                return (
                    <Grid item key={m.id}>
                        <Paper elevation={6} style={{padding: '15px'}}>
                            <Grid container alignItems={'center'} justifyContent={'center'} direction={'column'}>
                                <Grid item style={{textAlign: 'center'}}>
                                    <h3 style={{margin: '0px'}}>
                                        {m.name}
                                    </h3>
                                    <span style={{fontStyle: 'italic', fontSize: '15px'}}>Объём {m.volume} мл.</span>
                                </Grid>
                                <Grid item style={{margin: '10px 0px'}}>
                                    <img src={props.store.description.find(f => f.id === m.id)?.picture}
                                         alt={'Изображение'}/>
                                </Grid>
                                <Grid item>
                                    <div>Цена - <b>{m.priceRegular} руб.</b></div>
                                    <div>В день процедуры - <b>{m.priceDiscounted} руб.</b></div>
                                </Grid>
                            </Grid>
                            <Grid container style={{paddingTop: '25px'}}>
                                <Grid container justifyContent={'center'}>
                                    <ButtonGroup size="small" color={"primary"}>
                                        <Button variant={bottonsValue[m.id][0].valueAction ? 'contained' : 'outlined'}
                                                onClick={() => onClickChanger(m.id, 'valueAction')}>Действие</Button>
                                        <Button variant={bottonsValue[m.id][0].valueIngred ? 'contained' : 'outlined'}
                                                onClick={() => onClickChanger(m.id, 'valueIngred')}>Ингредиенты</Button>
                                        <Button variant={bottonsValue[m.id][0].valueApply ? 'contained' : 'outlined'}
                                                onClick={() => onClickChanger(m.id, 'valueApply')}>Как
                                            применять</Button>
                                    </ButtonGroup>
                                    {/*<Button variant={bottonsValue[m.id][0].valueAction ? 'contained' : 'outlined'}
                                            color={'primary'}
                                            style={{margin: '2px'}}
                                            onClick={() => onClickChanger(m.id, 'valueAction')}>Действие</Button>
                                    <Button variant={bottonsValue[m.id][0].valueIngred ? 'contained' : 'outlined'}
                                            color={'primary'}
                                            style={{margin: '2px'}}
                                            onClick={() => onClickChanger(m.id, 'valueIngred')}>Ингредиенты</Button>
                                    <Button variant={bottonsValue[m.id][0].valueApply ? 'contained' : 'outlined'}
                                            color={'primary'}
                                            style={{margin: '2px'}}
                                            onClick={() => onClickChanger(m.id, 'valueApply')}>Как применять</Button>*/}
                                </Grid>
                                {bottonsValue[m.id][0].valueAction && <Grid item>
                                    <ul>
                                        {props.store.description.find(f => f.id === m.id)?.action.map(m2 => <li
                                            key={v1()}>{m2}</li>)}
                                    </ul>
                                </Grid>}
                                {bottonsValue[m.id][0].valueIngred && <Grid item>
                                    <ul>
                                        {props.store.description.find(f => f.id === m.id)?.ingredients.map(m2 => <li
                                            key={v1()}>{m2}</li>)}
                                    </ul>
                                </Grid>}
                                {bottonsValue[m.id][0].valueApply && <Grid item>
                                    <div style={{
                                        textAlign: 'center',
                                        paddingTop: '15px'
                                    }}>{props.store.description.find(f => f.id === m.id)?.apply}</div>
                                </Grid>}
                            </Grid>
                        </Paper>
                    </Grid>
                )
            })}
        </Grid>
    )
}